
import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';
import { MapsAPILoader, AgmMap } from '@agm/core';
import { ModalController , NavParams} from '@ionic/angular';

import { Plugins, Capacitor , CallbackID} from '@capacitor/core';
const { Geolocation} = Plugins;


@Component({
  selector: 'app-my-modal',
  templateUrl: './my-modal.page.html',
  styleUrls: ['./my-modal.page.scss'],
})
export class MyModalPage implements OnInit {


  title = 'AGM project';
  latitude: number;
  longitude: number;
  zoom = 16 ;
  address: string;
  uubx: string;
  watchId: CallbackID;
  private geoCoder;
  tituloxx: any;

  @ViewChild('search', {static: false})
  public searchElementRef: ElementRef;


  constructor( private modalController: ModalController,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private navParams: NavParams
  ) { }

  dismissModal() {     this.modalController.dismiss('close');      }

  dadireccion() {     this.modalController.dismiss({direccion: this.address, la: this.latitude, lo: this.longitude});      }
  ngOnInit() {

    this.uubx = this.navParams.get('uubx');



    this.mapsAPILoader.load().then(() => {
      if (this.uubx === '') {     this.setCurrentLocationa();      } else      {  this.setCurrentLocation2a();      }
      this.geoCoder = new google.maps.Geocoder;

      const autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ['address']
      });
      autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {

          const place: google.maps.places.PlaceResult = autocomplete.getPlace();


          if (place.geometry === undefined || place.geometry === null) {
            return;
          }

          if (place) {
          this.latitude = place.geometry.location.lat();
          this.longitude = place.geometry.location.lng();
          this.getAddress(this.latitude, this.longitude);
          }
        });
      });
    });

  }

  loadMap() {
    this.mapsAPILoader.load().then(() => {
   this.setCurrentLocationa33();


    });
  }

  clearWatch() {
    if (this.watchId != null) {
      Plugins.Geolocation.clearWatch({ id: this.watchId });
    }
  }
  // Get Current Location Coordinates
  setCurrentLocationa33() {
    if (!Capacitor.isPluginAvailable('Geolocation')) {
      console.log('Plugin geolocation not available');
      return;
    }
    try {
      this.watchId = Plugins.Geolocation.watchPosition({timeout: 1000, enableHighAccuracy: true, maximumAge: 1000}, (data, err) => {
        if (data) {
          this.latitude = data.coords.latitude;
          this.longitude = data.coords.longitude;
      
  
        }
  
    });
  } catch (e) {
    console.error(e);
  }
  
  }
  setCurrentLocationa() {
  if (!Capacitor.isPluginAvailable('Geolocation')) {
    console.log('Plugin geolocation not available');
    return;
  }
  try {
    this.watchId = Plugins.Geolocation.watchPosition({timeout: 1000, enableHighAccuracy: true, maximumAge: 1000}, (data, err) => {
      if (data) {
        this.latitude = data.coords.latitude;
        this.longitude = data.coords.longitude;
        this.getAddress(this.latitude, this.longitude);

      }

  });
} catch (e) {
  console.error(e);
}

}

setCurrentLocation2a() {
  if (!Capacitor.isPluginAvailable('Geolocation')) {
    console.log('Plugin geolocation not available');
    return;
  }
  try {
    this.watchId = Plugins.Geolocation.watchPosition({timeout: 1000, enableHighAccuracy: true, maximumAge: 1000}, (data, err) => {
    this.latitude = Number( this.uubx.substring(this.uubx.indexOf('{') + 1, this.uubx.indexOf('¬∞') ));
    this.longitude = Number( this.uubx.substring(this.uubx.indexOf('¬∞') + 1, this.uubx.indexOf('}')) );
    if (this.latitude !== 0) {
      if (this.latitude) {
      this.getAddress(this.latitude, this.longitude);
      }
    }

  });
} catch (e) {
  console.error(e);
}

}




  markerDragEnd($event: any) {
    if (this.latitude !== 0) {
      if (this.latitude) {
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
    this.getAddress(this.latitude, this.longitude);
      }}
  }

  getAddress(latitude, longitude) {
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results, status) => {
      console.log(results);
      console.log(status);
      if (status === 'OK') {
        if (results[0]) {
          this.address = results[0].formatted_address;
        } else {
          window.alert('No results found');
        }
      } else {
        window.alert('Geocoder failed due to: ' + status);
      }

    });
  }

  async closeModal() {
    this.modalController.dismiss('close');
  }

}
